import loadingUrl from "@/assets/loading.svg";
// const loadingUrl = require("@/assets/loading.svg");

import styles from "./loading.module.less";
// const styles = require("./loading.module.less")

export default function (el, binding) {
    const curImg = el.querySelector('img[data-role=loading]'); //  "data-role=loading" <img>
    if (binding.value && !curImg) {
        const img = document.createElement('img');
        // HTMLElement.dataset  HTML DOM(data-*)
        // dataset 
        // HTML data-attribute DOM dataset.property 
        // 
        img.dataset.role = 'loading'; // querySelector
        img.src = loadingUrl;
        img.className = styles.loading;
        el.appendChild(img);
        console.log(img,loadingUrl);
    } else if (!binding.value && curImg) curImg.remove()
}