import Vue from 'vue';
/*
vuevue-routervue-routervue-router
npm install --legacy-peer-deps vue-router@3.5.2
 */
import VueRouter from 'vue-router' // VueRouter
import routerRules from '@/router/router-rules' // 

// Vue.use() vue
Vue.use(VueRouter);

// VueRouterrouterrouter
const router = new VueRouter({
	// 
	routes: routerRules,
	// history
	mode: 'hash', // hash
	// base: process.env.BASE_URL,
})

router.afterEach((to, from) => {
	if(to.meta.title){
		document.title = to.meta.title;
	}
})

export default router;